#map {
  position: absolute;
  top: 0;
  right: 0;
  width: 75%;
  height: 100%; }
  #map .map__refresh-map {
    position: absolute;
    left: 50%;
    top: 10px;
    z-index: 1001;
    -webkit-transition: ease-in-out 450ms linear;
    -o-transition: ease-in-out 450ms linear;
    transition: ease-in-out 450ms linear; }



